import PropTypes from 'prop-types';
// @mui
import { Stack, Link, Button, Typography, Card } from '@mui/material';
import { useNavigate } from 'react-router';
// utils
import { fDate } from '../../../../../utils/formatTime';
import { fCurrency } from '../../../../../utils/formatNumber';
// components
import Iconify from '../../../../../components/iconify';
import { PATH_DASHBOARD } from '../../../../../routes/paths';

// ----------------------------------------------------------------------

AccountBillingInvoiceHistory.propTypes = {
  invoices: PropTypes.array,
};

export default function AccountBillingInvoiceHistory({ invoices }) {
  console.log(invoices);
  const navigate = useNavigate()

  return (
    <Card sx={{ p: 3 }}>
      <Stack spacing={3} alignItems="flex-end">
        <Typography variant="overline" sx={{ width: 1, color: 'text.secondary' }}>
          Invoice History
        </Typography>
        <Stack direction="row" justifyContent="space-between" sx={{ width: 1 }}>
          <Typography variant="body2" sx={{ minWidth: 120 }}>
            Date
          </Typography>

          <Typography variant="body2" width={160} align='left' >Amount</Typography>

          <Typography sx={{
            textAlign: 'left',
            width: 160
          }} >
            Order Number
          </Typography>
          {/* <Button  ></Button> */}
        </Stack>
        <Stack spacing={2} sx={{ width: 1 }}>
          {invoices.map((invoice) => (
            <Stack key={invoice.id} direction="row" justifyContent="space-between" sx={{ width: 1 }}>
              <Typography variant="body2" sx={{ minWidth: 120 }}>
                {fDate(invoice.created_at)}
              </Typography>

              <Typography variant="body2" width={160} align='left' >{`${invoice?.countryzone?.currency_sign} ${invoice.order_total}`}</Typography>

              <Link onClick={() => {
                navigate(PATH_DASHBOARD.orders.OrdersDetaild(invoice.id))
              }} sx={{
                textAlign: 'left',
                width: 160,
                cursor: 'pointer'
              }} >
                <Typography variant="body2" width={160} align='left' >
                  ORD-{invoice?.order_no}
                </Typography>
              </Link>
              {/* <Button  ></Button> */}
            </Stack>
          ))}
        </Stack>
      </Stack>
    </Card>
  );
}
